import { FC, PropsWithChildren, forwardRef } from 'react';
import { styled } from '../../utils';
import { Size, TwBaseProps, TwStyleSystemProps } from '../../types';
import {
  Image as MantineImage,
  ImageProps as MantineImageProps,
  DefaultProps,
  MantineTheme,
} from '@mantine/core';

export interface ImageProps
  extends TwBaseProps,
    TwStyleSystemProps,
    PropsWithChildren,
    Omit<MantineImageProps, keyof DefaultProps> {
  shadowBorderSize?: Size;
}

const Root = styled(MantineImage)<MantineImageProps>(() => ({}));

export const Image: FC<ImageProps> = forwardRef((props, ref) => {
  const { shadowBorderSize, ...rest } = props;

  return <Root {...rest} ref={ref}
    styles={(theme: MantineTheme) => ({
      image: {
        ...(shadowBorderSize && {
          borderRadius: theme.radius[shadowBorderSize],
          boxShadow: theme.shadows[shadowBorderSize],
          border: '1px solid white'
        })
      }
    })}
  />;
});